.Tag{
    display: inline-block;
    border: 1px solid #9013FE;
    border-radius: 100px;
    margin-right: 8px;
}

.Tag-Content{
    font-family: Roboto-Italic;
    font-size: 16px;
    color: #9013FE;
    margin: 8px 16px;
}

.Tag-Content i{
    margin-left: 8px;
    cursor: pointer;
}


